import Util from "@/utils/util";
import Api from "@/utils/api";
import Rest from "@/utils/rest";

export default {
  data() {
    this.load = false;

    return {
      top_navs: [],
      curCat: 0,

      posts: [],
      loadMore: "more",
      changelist: 1,
    };
  },

  methods: {
    change(id) {
      this.changelist = id;
    },
	clickLink(link) {
      Util.openLink(link);
    },
    jqOnLoad() {
      this.loadSetting();
      this.loadPost();
    },

    jqOnShow() {
      if (!this.load) {
        this.load = true;
        this.jqOnLoad();
      }
    },

    lower() {
      if (this.loadMore == "more") {
        this.loadPost();
      }
    },

    clickPost(post_id) {
      Util.openLink("/pages/detail/detail?post_id=" + post_id);
    },

    clickJiangQie() {
      Util.jiangqie();
    },

    clickNavCat(cat_id) {
      this.curCat = cat_id;

      this.loadMore = "more";
      this.loaded = false;
      this.posts = [];

      this.loadPost();
    },

    loadSetting() {
      Rest.post(Api.JQ_OW_FREE_SETTING_NEWS, {}).then(
        (res) => {
          this.top_navs = [
            {
              id: 0,
              name: "全部",
            },
          ];
          this.top_navs = this.top_navs.concat(res.data.top_nav);
        },
        (err) => {
          console.log(err);
        }
      );
    },

    loadPost() {
      if (this.loadMore == "loading") {
        return;
      }
      this.loadMore = "loading";

      Rest.post(Api.JQ_OW_FREE_POSTS_LAST, {
        offset: this.posts.length,
        cat_id: this.curCat,
      }).then((res) => {
        this.posts = this.posts.concat(res.data.posts);
        this.loadMore = res.data.more;
      });
    },
  },
};
</script>

